#gameover{
  width: 80%;
  margin:auto;
}

#mensaje-puntaje-alto,#tres-winner{
  animation: blink 0.8s ease-out infinite alternate;
}

#mensaje-puntaje-alto{
  color: rgb(165, 171, 4);
}

@keyframes blink {
  from {
    transform: scale(1);
    filter: brightness(0.5);
  }
  to {
    transform: scale(1.2);
    filter: brightness(1);
  }
}