#s-tablero,.s-big-celda{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-template-rows: repeat(3,1fr);
  border: solid 0.5px black;
}

#s-tablero{
  width: 80%;
  margin: auto;
}

.s-celda{
  border: solid 0.5px rgb(0, 0, 0,0.5);
  margin: 0;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
}

