#tres-tablero{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  width: 80%;
  margin: auto;
}

.tres-celda{
  border: solid rgb(243, 243, 243);
  width: 100%;
  margin: 0;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  cursor: pointer;
  transform: all 0.4s;
}

.tres-celda:hover{
  background-color: #419376;
}

.X{color: rgb(242, 52, 38);}
.O{color: #55bece}


#t-juega{
  
  margin: 0;
}


@media screen and (min-width: 760px) {
  #tres-tablero{
    width: 30%;
  }
  h1{
    font-size: 1rem;
    margin: 0;
  }
  #t-juega{
    font-size: 0.8rem;
  }
}